        </div>
        <div style="width:92%;margin:0 4px;">
          <div class="input" style="margin-left:30px;">
            <span class="title" style="width:18%">姓名 :</span>
            <el-input style="width:74%;color:#000;" placeholder v-model="pname"></el-input>
          </div>
          <div class="input">
            <span class="title" style="width:24%">手机号 :</span>
            <el-input style="width:72%;color:#000;" placeholder v-model="ptel"></el-input>
          </div>
          <div class="input" style="width:40%">
            <span class="title" style="width:10%">邮箱 :</span>
            <el-input style="width:86%;color:#000;" placeholder v-model="pemail"></el-input>
          </div>
          <el-button @click="search" type icon="el-icon-search">搜索</el-button>
        </div>
        <el-table v-loading="loading" :data="tableData" stripe>
          <el-table-column prop="name" label="姓名"></el-table-column>
          <el-table-column prop="tel" label="手机号"></el-table-column>
          <el-table-column prop="ssqy" label="所属区域"></el-table-column>
          <el-table-column prop="ssbm" label="所属部门"></el-table-column>
          <el-table-column prop="zw" label="职务"></el-table-column>
          <el-table-column prop="sex" label="性别"></el-table-column>
          <el-table-column prop="email" label="邮箱" width="180"></el-table-column>
          <el-table-column prop="date" label="创建日期"></el-table-column>
        </el-table>
        <div class="page_fy">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page.sync="currentPage3"
            :page-size="page"
            prev-text="上一页"
            next-text="下一页"
            layout="prev, pager, next, jumper"
            :total="num"
          ></el-pagination>
        </div>
      </el-main>
    </el-container>
    <!-- 提示框 -->
    <el-dialog title="提示" :visible.sync="centerDialogVisible" width="30%" center>
      <span>{{err}}</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="centerDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="centerDialogVisible = false">确 定</el-button>
      </span>
    </el-dialog>
  </el-container>
</template>
