    },
    //页数函数
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    },
    //搜索函数
    search() {
      this.verification();
      this.loding = false;
    },
    //验证函数
    verification() {
      //验证姓名
      if (!/^[\u4e00-\u9fa5]{2,4}$/.test(this.pname) && this.pname !== "") {
        this.err = "请输入正确的姓名！";
        this.centerDialogVisible = true;
        return false;
      }

      //验证手机
      if (!/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.ptel) && this.ptel !== "") {
        this.err = "请输入正确的手机号码！";
        this.centerDialogVisible = true;
        return false;
      }
      //验证邮箱
      if (
        !/^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/.test(
          this.pemail
        ) &&
        this.pemail !== ""
      ) {
        this.err = "请输入正确的邮箱地址！";
        this.centerDialogVisible = true;
        return false;
      }
