}
.el-input__icon {
  line-height: 30px;
}
.el-button {
  padding: 6px 20px;
  margin: 0px 0px 10px 30px;
  overflow: hidden;
}
.el-table th,
.el-table tr {
  background: rgb(238, 241, 246) !important;
  color: #000 !important;
  font-weight: 600 !important;
  padding: 16px 0;
}
.el-table {
  width: 92%;
  height: 70%;
  margin-left: 30px;
}
/* 如果每页条数超过10条，可以用这个样式 --开始*/
.is-scrolling-none {
  height: 90%;
  overflow-y: scroll;
}
/*如果每页条数超过10条，可以用这个样式 --结束 */
.page_fy {
  width: 50%;
  margin: 20px auto;
  height: 50px;
}
.el-tree-node__content {
